import React from "react"
//import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import vista1 from "../images/V1_vista1.png"
import vista2 from "../images/V1_vista2.png"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    
    <h1>Bamboia V1</h1>
    <p>Un sistema capaz de entregar una elevada cantidad de pompas de jabón que además cuenta con varios modos, 
      uno lento que configura estructuras unidas de múltiples y grandes burbujas, 
      y otro más rápido que genera un mayor número de forma individual.</p>
    <img src={vista1} alt="Vista Bamboia V1" />
    <img src={vista2} alt="Vista Bamboia V1" />
    <h4 style={{ color: "darkred" }}>1r Test de página web <strong>(20/04/2020)</strong> (En construcción)</h4>
  </Layout>
)
//<Link to="/page-2/">Go to page 2</Link>
/* style={{ margin: 0 }}>
<h1>Primera generación!</h1>

*/
export default IndexPage
